import MainTitle from '~/components/shack/MainTitle.vue'
import SectionTitle from '~/components/shack/SectionTitle.vue'
import MembershipPlans from '~/components/shack/MembershipPlans.vue'
import { Select, Option } from 'element-ui'
import { mapState } from 'vuex'

export default {
  layout: 'ShackDash',
  components: {
    MainTitle,
    MembershipPlans,
    SectionTitle,
    [Select.name]: Select,
    [Option.name]: Option
  },
  async asyncData({ $plan, error }) {
    return await $plan
      .getAllPlans()
      .then(({ data, links, meta }) => {
        return {
          plans: data,
          links,
          meta
        }
      })
      .catch(e => {
        error({
          statusCode: e.statusCode,
          message: e.response
            ? JSON.stringify(e.response.data.errors)
            : e.message
        })
      })
  },
  data() {
    return {
      data: [],
      cards: [],
      admins: [],
      loading: false,
      searching: false,
      options: [
        { text: 'Referral', value: 'referral' },
        { text: 'Application', value: 'application' },
        { text: 'Past Events', value: 'past_events' },
        { text: 'Invite Card', value: 'invite_card' }
      ],
      prefix_type: [
        {
          text: 'Founding Member',
          value: '0'
        },
        {
          text: 'Fast Track',
          value: '1'
        },
        {
          text: 'Early Invite',
          value: '2'
        },
        {
          text: 'General Member',
          value: '3'
        }
      ],
      prefix_locality: [
        {
          text: 'Bay Area',
          value: 'A'
        },
        {
          text: 'US outside of the Bay',
          value: 'B'
        },
        {
          text: 'EMEA',
          value: 'C'
        },
        {
          text: 'APAC',
          value: 'D'
        }
      ],
      membership: {
        first_name: '',
        last_name: '',
        linkedin_url: '',
        membership_id: '',
        email: '',
        extras: [{ type: 'referral', comment: '' }],
        founding_member: false,
        trial: false,
        paid_for: false,
        trial_days: '0',
        prefix_type: '0',
        prefix_locality: 'A',
        paid_by: null,
        assigned_admin: null,
        trial_ends_at: null
      }
    }
  },
  computed: {
    ...mapState({
      space: state => state.space.currentSpace
    })
  },
  mounted() {
    this.membership.start_time = this.$moment().format('YYYY-MM-DD')
    this.membership.end_time = this.$moment()
      .add(1, 'year')
      .format('YYYY-MM-DD')
  },
  methods: {
    next() {
      const { next } = this.links
      this.$axios
        .$get(next)
        .then(({ data, links, meta }) => {
          this.plans = data
          this.links = links
          this.meta = meta
        })
        .catch(e => {
          this.$bvToast.toast(
            e.response ? JSON.stringify(e.response.data.errors) : e.message,
            {
              title: 'Error',
              variant: 'danger'
            }
          )
        })
    },
    prev() {
      const { prev } = this.links
      this.$axios
        .$get(prev)
        .then(({ data, links, meta }) => {
          this.plans = data
          this.links = links
          this.meta = meta
        })
        .catch(e => {
          this.$bvToast.toast(
            e.response ? JSON.stringify(e.response.data.errors) : e.message,
            {
              title: 'Error',
              variant: 'danger'
            }
          )
        })
    },
    toggleLoading() {
      this.loading = !this.loading
    },
    addNewMembership() {
      this.loading = !this.loading

      const { membership } = this
      membership.added_directly_into_directory = true
      membership.email = membership.email.toLowerCase()

      this.$membership
        .addMembership(membership)
        .then(res => {
          this.$bvToast.toast(
            'Membership created successfully. Proceed to invite member',
            {
              title: 'Membership Creation',
              variant: 'success'
            }
          )

          this.$router.push({ name: 'space-memberships-uninvited' })
        })
        .catch(e => {
          this.loading = !this.loading

          const message = e.response
            ? `${e.response.data.message} ~ ${JSON.stringify(
                e.response.data.errors
              )}`
            : e.message

          this.$bvToast.toast(message, {
            title: 'Error',
            variant: 'danger'
          })
        })
    },
    searchMembers(query) {
      const link = `filter[search]=${query}`

      this.searching = !this.searching

      const _self = this

      _.debounce(() => {
        _self.$membership.getAllMemberships(link).then(({ data }) => {
          _self.searching = !_self.searching
          _self.data = data
        })
      }, 350)()
    },
    searchAdmins(query) {
      const link = `/${this.space.subdomain}/admins?filter[name]=${query}`

      this.$admin
        .getAllAdmins(link)
        .then(res => {
          this.admins = res.data
        })
        .catch(escape => {
          const message = e.response
            ? `${e.response.data.message} ~ ${JSON.stringify(
                e.response.data.errors
              )}`
            : e.message
          error({
            statusCode: err.status || 404,
            message
          })
        })
    },
    getCards(e) {
      this.$membership
        .getPaymentMethods(e)
        .then(res => {
          // this.cards = res.data
          _.each(res.data, o => {
            this.cards.push({
              text: `${o.card_brand} - **** ${o.last_4}`,
              value: o.id
            })
          })
        })
        .catch(e => {
          const message = e.response
            ? `${e.response.data.message} ~ ${JSON.stringify(
                e.response.data.errors
              )}`
            : e.message
          this.$bvToast.toast(message, {
            variant: 'danger',
            title: 'Error'
          })
        })
    }
  }
}
